import {closeModal, openModal} from './modal';
import {postData} from '../services/services';

function forms(formSelector ,modalTimeout){
    const forms = document.querySelectorAll(formSelector);

    const message = {
        loading: 'img/form/spinner.svg',
        success: 'Success',
        failure: 'Error'
    };

    forms.forEach(item =>{
        bindPostData(item);
    });

    function bindPostData(form){
        form.addEventListener('submit', (event) =>{
            event.preventDefault();

            const statusMessage = document.createElement('img');
            statusMessage.src = message.loading;
            statusMessage.style = `
                display: block;
                margin: 0 auto;
            `;
            form.insertAdjacentElement('afterend', statusMessage);

            const formData = new FormData(form);

            // const objectJson = {};
            // formData.forEach((value, key) =>{
            //     objectJson[key] = value;
            // });
            const json = JSON.stringify(Object.fromEntries(formData.entries()));

            // fetch('server.php', {
            //     method: "POST", 
            //     headers: {
            //         'Content-type': 'application/json'
            //     },
            //     body: JSON.stringify(objectJson)
            // })
            postData('http://localhost:3000/requests', json)
            // .then(data => data.text())
            .then(data =>{
                console.log(data);
                showThanks(message.success);
            }).catch(()=>{
                showThanks(message.failure);
            }).finally(()=>{
                statusMessage.remove();
                event.target.reset();
            });

            // const json = JSON.stringify(objectJson);
            // const request = new XMLHttpRequest();
            // request.open('POST', 'server.php');
            // request.setRequestHeader('Content-type', 'application/json');
            // request.send(json);
            // request.addEventListener('load', () =>{
            //     if(request.status === 200){
            //         showThanks(message.success);
            //         event.target.reset();
            //         statusMessage.remove();
            //     }else{
            //         showThanks(message.failure);
            //         statusMessage.remove();
            //     }
            // });
        });
    }

    function showThanks(message){  
        const prevModal = document.querySelector('.modal__dialog');
        prevModal.classList.add('hide');
        openModal('.modal', modalTimeout);

        const thanksModal = document.createElement('div');
        thanksModal.classList.add('modal__dialog');
        thanksModal.innerHTML = `
            <div class = "modal__content">
                <div class = "modal__close" data-close>&times;</div>
                <div class = "modal__title">${message}</div>
            </div>
        `;
        document.querySelector(".modal").append(thanksModal);
        
        setTimeout(() =>{
            thanksModal.remove();
            prevModal.classList.add('show');
            prevModal.classList.remove('hide');
            closeModal('.modal');
        }, 3000);
    }
}

export default forms;